import { useState, useEffect, useRef } from 'react'
import { useAuthInfo } from '../hooks/authContext'
import { getAllProducts } from '../services/products'
import { getProductsByQuery } from '../services/products'
import ProductCard from '../components/ProductCard'
import mapboxgl from 'mapbox-gl'
import 'mapbox-gl/dist/mapbox-gl.css'
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder'
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css'
import { Collapse, Row, Col, Pagination, Skeleton  } from 'antd'

const { Panel } = Collapse

mapboxgl.accessToken = process.env.REACT_APP_MAPBOX_KEY

const Products = () => {
  const {user} = useAuthInfo()
  const [receivedProducts, setReceivedProducts] = useState(null)
  const [ownerLocations, setownerLocations] = useState(null)
  const mapContainer = useRef()
  const [map, setMap] = useState(null)
  const [lng, setLng] = useState(user && user.location.coordinates ? user.location.coordinates[0] : -70.9)
  const [lat, setLat] = useState(user && user.location.coordinates ? user.location.coordinates[1] : 42.35)
  const [collapseOpen, setCollapseOpen] = useState(true)
  const [collapseText, setCollapseText] = useState(<h3>Colse map</h3>)

  useEffect(() => {
    async function getProducts() {
      /* const query = {
        limit: 30,
        category: null,
        center: [lng, lat],
        radious: 1
      } */
      const {data:products} = await getAllProducts()
      setReceivedProducts(products.products)
      const locations = products.products.map(product => product.owner)
      const filteredLocations = locations.filter((thing, index, self) =>
        self.findIndex(t => t._id === thing._id) === index
      )
      console.log(filteredLocations)
      setownerLocations(filteredLocations)
    }
    getProducts()
  }, [])

  useEffect(() => {
    const map = new mapboxgl.Map({
      container: mapContainer.current,
      style: 'mapbox://styles/mapbox/streets-v11',
      center: [lng, lat],
      zoom: 12
    })

    const geocoder = new MapboxGeocoder({
      accessToken: mapboxgl.accessToken,
      mapboxgl: mapboxgl,
      marker: false,
      flyTo: {
        speed: 2
      }
    })
    
    map.addControl(geocoder)
    
    // Add zoom and rotation controls to the map.
    map.addControl(new mapboxgl.NavigationControl())

    // Add geolocate control to the map.
    map.addControl(
      new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true
        },
        showAccuracyCircle: false,
        showUserLocation: false
      })
    )

    const marker = new mapboxgl.Marker({
      draggable: true
    })
        .setLngLat([lng, lat])
        .addTo(map)

    map.on('movestart', function(e) {
      marker.setLngLat(map.getCenter())
    })
    
    map.on('move', function(e) {
      marker.setLngLat(map.getCenter())
    })
    
    map.on('moveend', function(e) {
      marker.setLngLat(map.getCenter())
    })

    map.on("load", () => {
      setMap(map)
    })

    return () => map.remove()
  }, [lng, lat])
  
  const returnToOriginalLocation = () => {
    map.flyTo({
      center: [lng, lat],
      speed: 2,
      essential: true // this animation is considered essential with respect to prefers-reduced-motion
    })
  }

  const changeCollapseState = () => {
    if (collapseOpen) {
      setCollapseText(<h3>Open map</h3>)
      setCollapseOpen(false)
    } else {
      setCollapseText(<h3>Close map</h3>)
      setCollapseOpen(true)
    }
  }

  return (
    <Row gutter={16}>
      <Col span={24}>
        <Collapse defaultActiveKey={['1']} bordered={false} ghost onChange={changeCollapseState}>
          <Panel header={collapseText} key="1">
            <div className="map-container" ref={mapContainer} style={{height: '400px'}} />
          </Panel>
        </Collapse>
      </Col>
      <Col flex="220px">
        <h3>filters</h3>
      </Col>
      <Col flex="auto">
        <Row gutter={[16, 16]} style={{padding: '20px'}} id='latest-prods'>
          {receivedProducts ? receivedProducts.map(product => (
            <ProductCard product={product} />
          )) : <Skeleton active />}
        </Row>
      </Col>
    </Row>
  )
}

export default Products
